@import '../../vars.css';

.CreateTodoButton {
    background-color: var(--tema);
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;

    transform: rotate(0);
    transition: .3s ease;
}

/* Estilos para el boton de añadir nueva tarea, bordes, sombras, fondo, fuente, posición, color, tamaño y animaciones */

.CreateTodoButton:hover {
    transform: rotate(224deg);
}

@media (max-width: 640px) {
    .CreateTodoButton {
        width: 100px;
        height: 100px;
    }
}